import React, { useState, useEffect } from "react";
import axios from "axios";
import "./EditarNegocio.css";

const EditarNegocio = () => {
  const [negocios, setNegocios] = useState([]);
  const [editBusiness, setEditBusiness] = useState(null);
  const [editedData, setEditedData] = useState({
    businessName: "",
    businessAddress: "",
    businessPhoneNumber: "",
  });

  const USER_ID = 7; // ID del usuario que queremos filtrar

  // Funci坦n para obtener los negocios desde la API
  const fetchNegocios = async () => {
    const negociosFiltrados = [];
    let id = 1;
    let keepFetching = true;

    try {
      while (keepFetching) {
        const response = await fetch(
          `https://api-degusto.onrender.com/api/business/${id}`
        );
        if (response.ok) {
          const data = await response.json();
          if (data.business_UserId === USER_ID && data.businessStatus === 1) {
            negociosFiltrados.push({
              id: negociosFiltrados.length + 1,
              businessId: data.businessId,
              businessName: data.businessName,
              businessAddress: data.businessAddress,
              businessPhoneNumber: data.businessPhoneNumber,
              businessStatus: data.businessStatus,
              businessLatitude: data.businessLatitude || 0,
              businessLongitude: data.businessLongitude || 0,
              businessCategorization: data.businessCategorization || 1,
              business_AreaId: data.business_AreaId || 1,
              business_UserId: data.business_UserId,
            });
          }
          id++;
        } else {
          keepFetching = false;
        }
      }
      setNegocios(negociosFiltrados);
    } catch (error) {
      console.error("Error al obtener negocios:", error);
    }
  };

  useEffect(() => {
    fetchNegocios();
  }, []);

  // Manejar cambios en los campos de entrada de edici坦n
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setEditedData({ ...editedData, [name]: value });
  };

  // Funci坦n para activar la edici坦n
  const handleEditClick = (negocio) => {
    setEditBusiness(negocio.businessId);
    setEditedData({
      businessName: negocio.businessName,
      businessAddress: negocio.businessAddress,
      businessPhoneNumber: negocio.businessPhoneNumber,
    });
  };

  // Funci坦n para guardar los cambios
  const handleSaveClick = async (id) => {
    try {
      const updatedBusiness = {
        businessName: editedData.businessName,
        businessAddress: editedData.businessAddress,
        businessPhoneNumber: editedData.businessPhoneNumber,
        businessStatus: 1,
        businessLatitude: negocios.find((n) => n.businessId === id)
          ?.businessLatitude,
        businessLongitude: negocios.find((n) => n.businessId === id)
          ?.businessLongitude,
        businessCategorization: 1,
        business_AreaId: 1,
        business_UserId: USER_ID,
      };

      await axios.put(
        `https://api-degusto.onrender.com/api/business/${id}`,
        updatedBusiness
      );

      setNegocios((prevNegocios) =>
        prevNegocios.map((negocio) =>
          negocio.businessId === id ? { ...negocio, ...updatedBusiness } : negocio
        )
      );
      setEditBusiness(null);
    } catch (error) {
      console.error("Error al actualizar el negocio:", error);
    }
  };

  // Funci坦n para eliminar un negocio
  const handleDeleteClick = async (id) => {
    try {
      await axios.delete(`https://api-degusto.onrender.com/api/business/${id}`);
      setNegocios((prevNegocios) =>
        prevNegocios.filter((negocio) => negocio.businessId !== id)
      );
    } catch (error) {
      console.error("Error al eliminar el negocio:", error);
    }
  };

  return (
    <div className="negocio-table-container">
      <h2>Lista de Negocios</h2>
      <table className="negocio-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>Nombre Negocio</th>
            <th>Direcci坦n</th>
            <th>N炭mero</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {negocios.map((negocio) => (
            <tr key={negocio.businessId}>
              <td>{negocio.id}</td>
              <td>
                {editBusiness === negocio.businessId ? (
                  <input
                    type="text"
                    name="businessName"
                    value={editedData.businessName}
                    onChange={handleInputChange}
                  />
                ) : (
                  negocio.businessName
                )}
              </td>
              <td>
                {editBusiness === negocio.businessId ? (
                  <input
                    type="text"
                    name="businessAddress"
                    value={editedData.businessAddress}
                    onChange={handleInputChange}
                  />
                ) : (
                  negocio.businessAddress
                )}
              </td>
              <td>
                {editBusiness === negocio.businessId ? (
                  <input
                    type="text"
                    name="businessPhoneNumber"
                    value={editedData.businessPhoneNumber}
                    onChange={handleInputChange}
                  />
                ) : (
                  negocio.businessPhoneNumber
                )}
              </td>
              <td>
                {editBusiness === negocio.businessId ? (
                  <>
                    <button
                      className="btn-guardar"
                      onClick={() => handleSaveClick(negocio.businessId)}
                    >
                      Guardar
                    </button>
                    <button
                      className="btn-cancelar"
                      onClick={() => setEditBusiness(null)}
                    >
                      Cancelar
                    </button>
                  </>
                ) : (
                  <>
                    <button
                      className="btn-editar"
                      onClick={() => handleEditClick(negocio)}
                    >
                      Editar
                    </button>
                    <button
                      className="btn-eliminar"
                      onClick={() => handleDeleteClick(negocio.businessId)}
                    >
                      Eliminar
                    </button>
                  </>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default EditarNegocio;