import React, { useState, useEffect } from 'react';
import './EditarPlato.css';

const EditarPlato = ({ plato }) => {
  const [dishData, setDishData] = useState({
    name: '',
    description: '',
    price: '',
    imageUrl: ''
  });

  const [imagePreview, setImagePreview] = useState(null);

  useEffect(() => {
    if (plato) {
      setDishData({
        name: plato.name,
        description: plato.description,
        price: plato.price,
        imageUrl: plato.imageUrl
      });
      setImagePreview(plato.imageUrl);
    }
  }, [plato]);

  const handleChange = (e) => {
    setDishData({
      ...dishData,
      [e.target.id]: e.target.value,
    });
  };

  const handleImageUpload = async (event) => {
    const file = event.target.files[0];
    const formData = new FormData();
    formData.append('file', file);
    formData.append('upload_preset', 'logo_preset'); 

    try {
      const res = await fetch('https://api.cloudinary.com/v1_1/derufjp1n/image/upload', {
        method: 'POST',
        body: formData
      });
      const data = await res.json();
      setImagePreview(data.secure_url);
      setDishData(prevData => ({
        ...prevData,
        imageUrl: data.secure_url
      }));
    } catch (err) {
      console.error('Error al cargar la imagen:', err);
    }
  };

  const handleEditDish = async () => {
    const dishDataToSend = {
      name: dishData.name,
      description: dishData.description,
      price: dishData.price,
      imageUrl: dishData.imageUrl,
    };

    try {
      const response = await fetch('https://api-degusto.onrender.com/#/Dish/getDishById', { // Ruta a tu API con el ID del plato
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(dishDataToSend),
      });
      if (response.ok) {
        alert('Plato editado exitosamente');
        setDishData({
          name: '',
          description: '',
          price: '',
          imageUrl: ''
        });
        setImagePreview(null);
      } else {
        alert('Error al editar el plato');
      }
    } catch (error) {
      console.error('Error al editar plato:', error);
    }
  };

  return (
    <section className="full-width header-well">
      <div className="full-width header-well-icon">
        <i className="zmdi zmdi-food"></i> {/* Icono de comida */}
      </div>
      <div className="mdl-grid">
        <div className="mdl-cell mdl-cell--12-col">
          <div className="full-width panel mdl-shadow--2dp">
            <div className="full-width panel-title bg-primary text-center tittles">
              Editar Plato
            </div>
            <div className="full-width panel-content">
              <form>
                <div className="mdl-grid">
                  <div className="mdl-cell mdl-cell--6-col">
                    <input
                      className="mdl-textfield__input"
                      type="text"
                      id="name"
                      value={dishData.name}
                      onChange={handleChange}
                      placeholder="Nombre del plato"
                    />
                  </div>
                  <div className="mdl-cell mdl-cell--6-col">
                    <input
                      className="mdl-textfield__input"
                      type="text"
                      id="description"
                      value={dishData.description}
                      onChange={handleChange}
                      placeholder="Descripci坦n (m叩x 300 caracteres)"
                      maxLength="300"
                    />
                  </div>
                  <div className="mdl-cell mdl-cell--6-col">
                    <input
                      className="mdl-textfield__input"
                      type="number"
                      id="price"
                      value={dishData.price}
                      onChange={handleChange}
                      placeholder="Precio"
                    />
                  </div>
                  <div className="mdl-cell mdl-cell--12-col">
                    <div style={{ height: '200px', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                      {imagePreview ? (
                        <img src={imagePreview} alt="Previsualizaci坦n de imagen" style={{ maxWidth: '100%', maxHeight: '100%' }} />
                      ) : (
                        <div>Imagen no cargada</div>
                    )}
                      </div>
                    <input type="file" accept="image/*" onChange={handleImageUpload} />
                  </div>
                  <div className="mdl-cell mdl-cell--12-col">
                    <button
                      type="button"
                      className="mdl-button mdl-js-button mdl-button--raised mdl-button--colored"
                      onClick={handleEditDish}
                    >
                      Editar Plato
                    </button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default EditarPlato;