import React, { useEffect, useState } from "react";
import axios from "axios";
import "./VistaUsuario.css";

const VistaUsuario = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true); // Indicador de carga
  const [error, setError] = useState(null); // Manejo de errores

  // Funci坦n para obtener los usuarios desde la API
  const fetchUsers = async () => {
    try {
      const response = await axios.get("https://api-degusto.onrender.com/api/user");
      const filteredUsers = response.data.filter(
        (user) => user.personTypeName === "Owner" || user.personTypeName === "Customer"
      );
      setUsers(filteredUsers);
    } catch (err) {
      console.error("Error al obtener los usuarios:", err);
      setError("No se pudieron cargar los usuarios.");
    } finally {
      setLoading(false);
    }
  };

  // Efecto para cargar los usuarios al montar el componente
  useEffect(() => {
    fetchUsers();
  }, []);

  // Funci坦n para actualizar el estado del usuario (Validar, Denegar o Eliminar)
  const updateUserStatus = async (userId, newStatus) => {
    try {
      // Si el estado es "Eliminado", hacemos una llamada DELETE a la API
      if (newStatus === "Eliminado") {
        await axios.delete(`https://api-degusto.onrender.com/api/user/${userId}`);
        setUsers((prevUsers) => prevUsers.filter((user) => user.userId !== userId));
      } else {
        await axios.put(`https://api-degusto.onrender.com/api/user/${userId}`, {
          userStatus: newStatus,
        });
        setUsers((prevUsers) =>
          prevUsers.map((user) =>
            user.userId === userId ? { ...user, userStatus: newStatus } : user
          )
        );
      }
    } catch (err) {
      console.error("Error al actualizar el estado del usuario:", err);
    }
  };

  // Mostrar indicador de carga
  if (loading) return <div>Cargando usuarios...</div>;
  if (error) return <div>{error}</div>;

  return (
    <div className="user-table-container">
      <h2>Lista de Usuarios</h2>
      <table className="user-table">
        <thead>
          <tr>
            <th>Nro</th>
            <th>Usuario</th>
            <th>Nombre</th>
            <th>Email</th>
            <th>Tel辿fono</th>
            <th>Tipo</th>
            <th>Fecha de creaci坦n</th>
            <th>Estado</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {users.map((user, index) => (
            <tr key={user.userId}>
              <td>{index + 1}</td>
              <td>{user.userName}</td>
              <td>{user.PersonName}</td>
              <td>{user.personEmail}</td>
              <td>{user.personPhoneNumber}</td>
              <td>{user.personTypeName === "Owner" ? "Due単o" : "Cliente"}</td>
              <td>{new Date(user.userCreatedAt).toLocaleString()}</td>
              <td>
                <span
                  className={`estado-${user.userStatus === 1 ? "validado" : user.userStatus === 0 ? "pendiente" : "eliminado"}`}
                >
                  {user.userStatus === 1
                    ? "Validado"
                    : user.userStatus === 0
                    ? "Pendiente"
                    : "Eliminado"}
                </span>
              </td>
              <td>
                {user.userStatus !== "Eliminado" && (
                  <>
                    <button
                      className="btn-validar"
                      onClick={() => updateUserStatus(user.userId, 1)}
                    >
                      Validar
                    </button>
                    <button
                      className="btn-denegar"
                      onClick={() => updateUserStatus(user.userId, 0)}
                    >
                      Denegar
                    </button>
                  </>
                )}
                <button
                  className="btn-eliminar"
                  onClick={() => updateUserStatus(user.userId, "Eliminado")}
                >
                  Eliminar
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default VistaUsuario;