import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from "axios"; 
import './MenuFunctions.css';
import Resumen from './Resumen.js';
import AgregarNegocio from './AgregarNegocio.js';
import EditarNegocio from './EditarNegocio.js';
import AgregarPlato from './AgregarPlato.js';
import EditarPlato from './EditarPlato.js';
import TusReservas from './TusReservas.js';  // Aseg炭rate de crear este componente si no existe.

const MenuFunctions = () => {
  const [activeComponent, setActiveComponent] = useState(""); 
  const navigate = useNavigate();

  const loadComponent = (component) => {
    setActiveComponent(component);
  };

  const renderComponent = () => {
    switch (activeComponent) {
      case "resumen":
        return <Resumen />;
      case "agregarnegocio":
        return <AgregarNegocio />;
      case "editarnegocio":
        return <EditarNegocio />;
      case "agregarplato":
        return <AgregarPlato />;
      case "editarplato":
        return <EditarPlato />;
      case "tusreservas":  // Nueva opci坦n
        return <TusReservas />;
      default:
        return (
          <div className="dashboard">
            <h2>臓Bienvenido al Panel de Funciones!</h2>
            <p>Selecciona una opci坦n del men炭 para empezar.</p>
          </div>
        );
    }
  };

  const handleLogout = async () => {
    try {
      const response = await axios.post("https://api-degusto.onrender.com/api/user/logout");
      if (response.status === 200) {
        console.log('Sesi坦n cerrada');
        navigate("/loginregister");
      } else {
        console.error("Error al cerrar sesi坦n:", response.data);
        alert("Hubo un problema al cerrar sesi坦n.");
      }
    } catch (error) {
      console.error("Error de red al cerrar sesi坦n:", error);
      alert("Error de red. Por favor, intenta nuevamente.");
    }
  };

  return (
    <div className="owner-container">
      <nav className="owner-menu">
        <h3 className="owner-title">Panel de Funciones</h3>
        <ul className="owner-list">
          {[ 
            { key: "resumen", label: "Resumen" },
            { key: "agregarnegocio", label: "Agregar Negocio" },
            { key: "editarnegocio", label: "Tus Negocios" },  // Cambio aqu鱈
            { key: "agregarplato", label: "Agregar Plato" },
            { key: "editarplato", label: "Tus Platos" },  // Cambio aqu鱈
            { key: "tusreservas", label: "Tus Reservas" }  // Nueva opci坦n aqu鱈
          ].map(({ key, label }) => (
            <li
              key={key}
              className={activeComponent === key ? "active" : ""}
              onClick={() => loadComponent(key)}
            >
              {label}
            </li>
          ))}
        </ul>
      </nav>
      <div className="owner-main">
        <header className="owner-header">
          <span>Bienvenido, Due単o de Negocio!</span>
          <button className="btn-logout" onClick={handleLogout}>Cerrar Sesi坦n</button>
        </header>
        <div className="content">{renderComponent()}</div>
      </div>
    </div>
  );
};

export default MenuFunctions;